* {
  font-family: Helvetica, Arial,  sans-serif;
}

body {
  padding: 60px;
}

input {
  color: pink;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 5px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}